.right-panel__tabs {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 10px 0;
}

.right-panel__tabs-button {
  width: 40%;
  height: 35px;
  background-color: black;
  color: white;
  outline: none;
  border: 4px solid black;
  cursor: pointer;
  font-size: 15px;

  &.active {
    background-color: darkred;
    color: black;
    font-weight: 900;
  }

  &:hover {
    background-color: red;
    color: black;
    font-weight: 900;
  }
}