.right-panel__layout-block {
  cursor: pointer;
  padding: 10px;
  width: 350px;
  display: flex;
  justify-content: flex-end;
  padding-left: 25px;
  padding-right: 30px;
  padding-bottom: 50px;
  position: relative;

  canvas {
    width: 250px;
    height: auto;
    outline: 3px solid red;
  }
}

.right-panel__layout-list {
  overflow: hidden;
  overflow-y: scroll;
  max-height: 700px;
  position: relative;
}

.right-panel__layout-buttons {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;

  button {
    width: 40%;
    padding: 5px;
    background-color: black;
    border: none;
    color: whitesmoke;
    cursor: pointer;
    border: 4px solid black;

    &:hover {
      background-color: darkred;
    }
  }
}

.right-panel__context-menu {
  position: absolute;
  display: none;
  background-color: red;
  z-index: 999;
  width: 200px;
  border-radius: 10px;

  &.active {
    display: block;
  }
}

.right-panel__context-menu-item {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10px;

  &:hover {
    background-color: gray;
  }
}