.hide-current-element-warning {
  display: block;
  position: absolute;
  width: 500px;
  height: 250px;
  background-color: black;
  color: red;
  border: 5px solid red;
  top: 0px;
  left: 50%;
  // transform: translateX(-50%);
  font-size: 30px;
  font-weight: 900;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
}

.layout-block__left-menu {
  position: absolute;
  left: 10px;
  top: 10px;
  bottom: 50px;
  width: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.layout-block__left-menu-item {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  img {
    width: 25px;
    height: 25px;
  }
}

.layout-block__bottom-menu {
  position: absolute;
  bottom: 0;
  left: 10px;
  height: 40px;
  width: 310px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.layout-block__bottom-menu-item {
  height: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;

  img {
    width: 40px;
    height: 40px;
  }

  &.history {
    position: absolute;
    left: 0;
    top: -3px;
    font-size: 20px;
    color: whitesmoke;
    cursor: default;
  }

  &.back {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 80px;
  }

  &.next {
    position: absolute;
    width: 40px;
    height: 40px;
    left: 240px;
  }

  &.favorites {
    position: absolute;
    left: 137px;
    padding: 5px;

    &:hover {
      background-color: rgb(117, 0, 0);
    }
  }
}